import time from "@/common/time.js";
//模拟当前登录用户的userId
const uid=1;
export default{
	props:{
		item:Object,
		index:Number,
		pretime:[Number,String]
	},
	computed:{
		isSelf(){
			return uid==this.item.userId
		},
		//转化时间
		shortTime(){
			return time.gettime.getChatTime(this.item.createTime,this.pretime)
		}
	}
}
